.logo {
    height: 90px;
    width: 80%;
    padding: 0% 10%;
}

.logo img {
    padding: 8px 0px;
}

.nav_bar {
    width: 80%;
    height: 50px;
    color: rgb(192, 194, 194);
    background-color: rgb(74, 78, 81);
    display: flex;
    padding: 0% 10%;
    margin-bottom: 41px;
}

.nav_bar ul {
    list-style: none;
    display: inline-block;
}

.nav_bar li {
    display: inline-flex;
    text-align: center;
    font-size: 14px;
    line-height: 50px;
    padding: 0px 12px;
    color: rgb(192, 194, 194);
}

.nav_bar li:hover {
    background-color: rgb(59, 62, 65);
}

.nav_bar .menu {
    display: none;
}

@media screen and (max-width: 1024px) {

    .logo,
    .nav_bar {
        width: 98%;
        padding: 0% 1%;
    }

    .logo img {
        padding: 9px 0px;
    }
}

@media screen and (max-width: 653px) {
    .nav_bar .box {
        display: none;
    }

    .nav_bar .menu {
        display: inline-block;
        line-height: 50px;
    }

    .mobile_menu {
        display: block;
        height: 100%;
        width: 0;
        position: fixed;
        overflow: hidden;
        z-index: 1;
        top: 0;
        left: 0;
        background-color: rgba(0, 0, 0, 0.97);
        transition: 0.5s;
    }

    .mobile_menu .close {
        color: #818181;
        text-align: right;
        margin-top: 15px;
        margin-right: 25px;
    }

    .mobile_menu .list {
        font-size: 24px;
        padding: 30px 8px 0px;
        color: #818181;
        display: block;
        transition: 0.3s;
        text-align: left;
    }

    .mobile_menu .box p {
        padding: 4px 20px;
        width: 100%;
    }

    .list li {
        list-style: none;
        margin: 15px 10%;
        width: 80%;
        font-size: 22px;
        font-weight: 400;
        color: rgb(138, 138, 138);
    }
}