* {
  margin: 0;
  padding: 0;
}

body {
  width: 100%;
  height: 100%;
  font-family: "Open Sans", Arial, sans-serif;
  font-style: normal;
  font-weight: 900;
  background-color: rgb(246, 248, 249);
}
.logo {
    height: 90px;
    width: 80%;
    padding: 0% 10%;
}

.logo img {
    padding: 8px 0px;
}

.nav_bar {
    width: 80%;
    height: 50px;
    color: rgb(192, 194, 194);
    background-color: rgb(74, 78, 81);
    display: -webkit-flex;
    display: flex;
    padding: 0% 10%;
    margin-bottom: 41px;
}

.nav_bar ul {
    list-style: none;
    display: inline-block;
}

.nav_bar li {
    display: -webkit-inline-flex;
    display: inline-flex;
    text-align: center;
    font-size: 14px;
    line-height: 50px;
    padding: 0px 12px;
    color: rgb(192, 194, 194);
}

.nav_bar li:hover {
    background-color: rgb(59, 62, 65);
}

.nav_bar .menu {
    display: none;
}

@media screen and (max-width: 1024px) {

    .logo,
    .nav_bar {
        width: 98%;
        padding: 0% 1%;
    }

    .logo img {
        padding: 9px 0px;
    }
}

@media screen and (max-width: 653px) {
    .nav_bar .box {
        display: none;
    }

    .nav_bar .menu {
        display: inline-block;
        line-height: 50px;
    }

    .mobile_menu {
        display: block;
        height: 100%;
        width: 0;
        position: fixed;
        overflow: hidden;
        z-index: 1;
        top: 0;
        left: 0;
        background-color: rgba(0, 0, 0, 0.97);
        transition: 0.5s;
    }

    .mobile_menu .close {
        color: #818181;
        text-align: right;
        margin-top: 15px;
        margin-right: 25px;
    }

    .mobile_menu .list {
        font-size: 24px;
        padding: 30px 8px 0px;
        color: #818181;
        display: block;
        transition: 0.3s;
        text-align: left;
    }

    .mobile_menu .box p {
        padding: 4px 20px;
        width: 100%;
    }

    .list li {
        list-style: none;
        margin: 15px 10%;
        width: 80%;
        font-size: 22px;
        font-weight: 400;
        color: rgb(138, 138, 138);
    }
}
.section_to_left,
.section_to_right,
.section_left,
.section_right,
.section_center {
    width: 100%;
    height: auto;
    margin-top: 40px;
    font-size: 22px;
}

.title {
    width: 100%;
    height: auto;
    text-align: center;
    font-size: 4.12796697626419vh;
}

.title_underline {
    border: solid 1px #ff6600;
    height: 0.25vw;
    background-color: #ff6600;
    width: 5%;
    margin: 25px 47.5%;
}

.section_text {
    width: 52%;
    padding: 0% 24%;
    text-align: justify;
    word-spacing: 3px;
    font-size: 2.2703818369453046vh;
    line-height: 3.611971104231166vh;
    font-weight: 300;
}

.section_images {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    width: 86%;
    margin: 5% 7%;
    -webkit-justify-content: space-around;
            justify-content: space-around;
}

.image_left img,
.image_right img {
    width: 100%;
    height: auto;
}

.image_center img {
    display: block;
    margin: 0% auto;
    width: 97%;
}

.keywords {
    padding: 0% 5%;
}

.keywords .keywords_title {
    color: black;
    font-size: 4.12796697626419vh;
}

.keywords p {
    color: #ff6600;
    font-size: 2.2703818369453046vh;
    line-height: 60px;
    font-weight: 400;
    text-align: center;
}

.main_image {
    width: 100%;
    text-align: center;
}

.main_image img {
    height: auto;
    width: 100%;
}

@media screen and (max-width: 1660px) {

    .section_to_left .image_right,
    .section_to_right .image_left {
        display: none;
    }

    .section_to_left .image_left,
    .section_to_right .image_right,
    .section_left .image_left,
    .section_right .image_right,
    .section_center .image_center {
        -webkit-order: -1;
                order: -1;
    }

    .section_images {
        -webkit-flex-wrap: wrap;
                flex-wrap: wrap;
        width: 98%;
        margin: 5% 1%;
        -webkit-justify-content: center;
                justify-content: center;
    }

    .section_text {
        width: 90%;
        padding: 0% 5%;
    }

    .keywords {
        padding: 5%;
    }

    .keywords p {
        line-height: 40px;
    }
}

@media screen and (max-width: 768px) {

    .section_to_left,
    .section_right,
    .section_left,
    .section_right,
    .section_center {
        width: 98%;
        padding: 0% 1%;
    }

    .section_images {
        width: 100%;
        margin: 5% 0%;
    }

    .main_image {
        padding: 0% 0%;
        width: 100%;
    }

    .image_center img {
        margin: 0%;
        width: 100%;
    }
}
.container {
    width: 80%;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    border-top: solid #e2e2e2 1px;
    margin-bottom: 10px;
    padding: 0% 10%;
}

.content {
    width: 100%;
    height: auto;
    background-color: white;
    border-left: solid #e2e2e2 1px;
    border-right: solid #e2e2e2 1px;
    border-bottom: solid #e2e2e2 1px;
}

@media screen and (max-width: 1024px) {
    .container {
        width: 98%;
        padding: 0% 1%;
    }
}
.contact-social {
    width: 100%;
    text-align: center;
}

.contact-social i {
    padding: 25px;
    color: #ff6600;
}

.contact-logo {
    text-align: center;
    padding: 6% 0% 2%;
}

@media screen and (max-width: 653px) {
    .contact-social i {
        padding: 8px;
    }
}
.section_text p {
    margin: 50px 0px 150px;
    text-align: center;
}

.section_text button {
    width: 300px;
    height: 30px;
    font-size: 22px;
    font-weight: bold;
    border: none;
    border-radius: 5px;
    color: white;
    background-color: rgb(74, 78, 81);
}

.section_text button:hover {
    background-color: #ff6600;
}

@media screen and (max-width: 768px) {
    .section_text button {
        background-color: #ff6600;
    }
}
.car {
    display: -webkit-flex;
    display: flex;
    width: 80%;
    padding: 0% 10%;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
}

.car .car_text {
    width: 50%;
    padding: 0% 5%;
    font-weight: 300;
    font-size: 2vh;
    word-spacing: 3px;
    text-align: justify;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
}

.car .car_image {
    width: 40%;
}

.car .car_image img {
    width: 100%;
    height: auto;
}

.car .car_text .title {
    padding-bottom: 2%;
    font-weight: 700;
}

.car .preview {
    display: block;
    text-align: center;
    height: 100px;
    width: 100%;
    margin: 1% auto 6%;
}

.car .preview button {
    width: 300px;
    height: 30px;
    font-size: 22px;
    font-weight: bold;
    line-height: 30px;
    border: none;
    border-radius: 5px;
    color: white;
    background-color: rgb(74, 78, 81);
    display: block;
    margin: 3% auto;
}

.car .preview button:hover {
    background-color: #ff6600;
}

.dealer_image {
    width: 79.6%;
    margin: 3% 10.20%;
}

.dealer_image img {
    display: block;
    margin: 0% auto;
    width: 100%;
}

@media screen and (max-width: 768px) {
    .car {
        width: 98%;
        padding: 0% 1%;
    }

    .dealer_image {
        display: none;
    }

    .car .car_image {
        width: 100%
    }

    .car .car_text {
        width: 98%;
        padding: 5% 1%;
        font-size: 2.2703818369453046vh;
    }

    .car .car_text .title {
        padding-bottom: 10%;
    }

    .car .preview {
        padding: 0% 0%;
        margin: 0% auto;
    }

    .car .preview button {
        background-color: #ff6600;
    }
}
.important_links {
    width: 80%;
    height: auto;
    padding: 0% 10%;
    text-align: right;
    font-size: 14px;
    line-height: 50px;
    border-top: solid #e2e2e2 1px;
    border-bottom: solid #e2e2e2 1px;
}

.important_links a {
    text-decoration: none;
    padding: 0px 5px;
    color: #aaaaaa;
}

.important_links a:hover {
    color: rgb(73, 147, 197);
    text-decoration: underline;
}

.socials {
    width: 80%;
    height: auto;
    padding: 24px 10%;
    text-align: right;
}

.socials a {
    text-decoration: none;
    margin: 0px 2px;
}

@media screen and (max-width: 1024px) {

    .important_links,
    .socials {
        width: 98%;
        padding: 0% 1%;
    }
}

@media screen and (max-width: 653px) {

    .important_links,
    .socials {
        text-align: center;
    }

    .important_links {
        display: none;
    }

    .socials {
        height: 50px;
        padding-top: 10px;
    }
}
