.car {
    display: flex;
    width: 80%;
    padding: 0% 10%;
    flex-wrap: wrap;
}

.car .car_text {
    width: 50%;
    padding: 0% 5%;
    font-weight: 300;
    font-size: 2vh;
    word-spacing: 3px;
    text-align: justify;
    display: flex;
    flex-direction: column;
}

.car .car_image {
    width: 40%;
}

.car .car_image img {
    width: 100%;
    height: auto;
}

.car .car_text .title {
    padding-bottom: 2%;
    font-weight: 700;
}

.car .preview {
    display: block;
    text-align: center;
    height: 100px;
    width: 100%;
    margin: 1% auto 6%;
}

.car .preview button {
    width: 300px;
    height: 30px;
    font-size: 22px;
    font-weight: bold;
    line-height: 30px;
    border: none;
    border-radius: 5px;
    color: white;
    background-color: rgb(74, 78, 81);
    display: block;
    margin: 3% auto;
}

.car .preview button:hover {
    background-color: #ff6600;
}

.dealer_image {
    width: 79.6%;
    margin: 3% 10.20%;
}

.dealer_image img {
    display: block;
    margin: 0% auto;
    width: 100%;
}

@media screen and (max-width: 768px) {
    .car {
        width: 98%;
        padding: 0% 1%;
    }

    .dealer_image {
        display: none;
    }

    .car .car_image {
        width: 100%
    }

    .car .car_text {
        width: 98%;
        padding: 5% 1%;
        font-size: 2.2703818369453046vh;
    }

    .car .car_text .title {
        padding-bottom: 10%;
    }

    .car .preview {
        padding: 0% 0%;
        margin: 0% auto;
    }

    .car .preview button {
        background-color: #ff6600;
    }
}