.section_text p {
    margin: 50px 0px 150px;
    text-align: center;
}

.section_text button {
    width: 300px;
    height: 30px;
    font-size: 22px;
    font-weight: bold;
    border: none;
    border-radius: 5px;
    color: white;
    background-color: rgb(74, 78, 81);
}

.section_text button:hover {
    background-color: #ff6600;
}

@media screen and (max-width: 768px) {
    .section_text button {
        background-color: #ff6600;
    }
}