.container {
    width: 80%;
    display: flex;
    flex-direction: row;
    border-top: solid #e2e2e2 1px;
    margin-bottom: 10px;
    padding: 0% 10%;
}

.content {
    width: 100%;
    height: auto;
    background-color: white;
    border-left: solid #e2e2e2 1px;
    border-right: solid #e2e2e2 1px;
    border-bottom: solid #e2e2e2 1px;
}

@media screen and (max-width: 1024px) {
    .container {
        width: 98%;
        padding: 0% 1%;
    }
}