.important_links {
    width: 80%;
    height: auto;
    padding: 0% 10%;
    text-align: right;
    font-size: 14px;
    line-height: 50px;
    border-top: solid #e2e2e2 1px;
    border-bottom: solid #e2e2e2 1px;
}

.important_links a {
    text-decoration: none;
    padding: 0px 5px;
    color: #aaaaaa;
}

.important_links a:hover {
    color: rgb(73, 147, 197);
    text-decoration: underline;
}

.socials {
    width: 80%;
    height: auto;
    padding: 24px 10%;
    text-align: right;
}

.socials a {
    text-decoration: none;
    margin: 0px 2px;
}

@media screen and (max-width: 1024px) {

    .important_links,
    .socials {
        width: 98%;
        padding: 0% 1%;
    }
}

@media screen and (max-width: 653px) {

    .important_links,
    .socials {
        text-align: center;
    }

    .important_links {
        display: none;
    }

    .socials {
        height: 50px;
        padding-top: 10px;
    }
}