.section_to_left,
.section_to_right,
.section_left,
.section_right,
.section_center {
    width: 100%;
    height: auto;
    margin-top: 40px;
    font-size: 22px;
}

.title {
    width: 100%;
    height: auto;
    text-align: center;
    font-size: 4.12796697626419vh;
}

.title_underline {
    border: solid 1px #ff6600;
    height: 0.25vw;
    background-color: #ff6600;
    width: 5%;
    margin: 25px 47.5%;
}

.section_text {
    width: 52%;
    padding: 0% 24%;
    text-align: justify;
    word-spacing: 3px;
    font-size: 2.2703818369453046vh;
    line-height: 3.611971104231166vh;
    font-weight: 300;
}

.section_images {
    display: flex;
    flex-direction: row;
    width: 86%;
    margin: 5% 7%;
    justify-content: space-around;
}

.image_left img,
.image_right img {
    width: 100%;
    height: auto;
}

.image_center img {
    display: block;
    margin: 0% auto;
    width: 97%;
}

.keywords {
    padding: 0% 5%;
}

.keywords .keywords_title {
    color: black;
    font-size: 4.12796697626419vh;
}

.keywords p {
    color: #ff6600;
    font-size: 2.2703818369453046vh;
    line-height: 60px;
    font-weight: 400;
    text-align: center;
}

.main_image {
    width: 100%;
    text-align: center;
}

.main_image img {
    height: auto;
    width: 100%;
}

@media screen and (max-width: 1660px) {

    .section_to_left .image_right,
    .section_to_right .image_left {
        display: none;
    }

    .section_to_left .image_left,
    .section_to_right .image_right,
    .section_left .image_left,
    .section_right .image_right,
    .section_center .image_center {
        order: -1;
    }

    .section_images {
        flex-wrap: wrap;
        width: 98%;
        margin: 5% 1%;
        justify-content: center;
    }

    .section_text {
        width: 90%;
        padding: 0% 5%;
    }

    .keywords {
        padding: 5%;
    }

    .keywords p {
        line-height: 40px;
    }
}

@media screen and (max-width: 768px) {

    .section_to_left,
    .section_right,
    .section_left,
    .section_right,
    .section_center {
        width: 98%;
        padding: 0% 1%;
    }

    .section_images {
        width: 100%;
        margin: 5% 0%;
    }

    .main_image {
        padding: 0% 0%;
        width: 100%;
    }

    .image_center img {
        margin: 0%;
        width: 100%;
    }
}